window.addEventListener("load", () => {
    // Line drawing technique is from:
    // https://jakearchibald.com/2013/animated-line-drawing-svg
    const path = (document.querySelector(".header--logo") as HTMLObjectElement).contentDocument.getElementById(
        "logoPath"
    ) as unknown as SVGPathElement;
    const length = path.getTotalLength() / 7;

    path.style.strokeDasharray = length + " " + length;
    path.style.strokeDashoffset = String(length);
    // Trigger a layout
    path.getBoundingClientRect();
    path.style.transition = "stroke-dashoffset 2s ease-in-out";
    path.style.strokeDashoffset = "0";

    // Rewire local hrefs (thanks bs-dismiss)
    document.querySelectorAll(".a-local").forEach(re => {
        const e = re as HTMLAnchorElement;
        e.addEventListener("click", () => {
            setTimeout(() => {
                location.hash = e.dataset.href;
            }, 600);
        });
    });
});

const elems = document.querySelectorAll(".anim");

function handleAnimations() {
    const wh = window.innerHeight;
    for (const e of elems) {
        const y = e.getBoundingClientRect().y;
        if (y < wh) {
            e.classList.add("anim-on");
        } else {
            e.classList.remove("anim-off");
        }
    }
}

function main() {
    handleAnimations();
    requestAnimationFrame(main);
}
requestAnimationFrame(main);
